input[type="search"].form-input{
  margin: 0;
  padding-right: .75em;
  width: 100%;
  max-width: 216px;
  height: 44px;
  line-height: 44px;

  border-radius: 1.5em;

  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;

  font-weight: bold;

  position: relative;

  background-color: transparent;
  color: $color-lightgrey;
  border: 2px solid $color-lightgrey;

  text-indent: 1.5em;

  cursor: pointer;
  outline: 0;

  @include media-query('small') {
    max-width: unset;   
  }
}

fieldset.fieldset{
  all: unset;

  &--with-label{
    input{
      margin-top: .5em;
    }
  }
}

select.select{
  padding: 0 .5em;
  position: relative;

  &--naked{
    background: unset;
    border: unset;
    color: inherit;
  }
}

option.select-item{
    font-weight: bold;
}

label{
  padding: .5em 0 1em;
}

button{
  all: unset;
}

.form-login{
  margin: 2em 0;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto;
  grid-gap: 2em;

  width: 400px;

  @include media-query('small') {
    width: 320px;
  }

  .form-input,
  button.button{
    width: 100%;
  }
}

.form-input{
  padding: .125em 0.25em;
  outline: 0;
  cursor: pointer;

  &--with-label{
    margin-top: 1em;
  }

  &--default{
    display: block;
  
    height: 44px;
    min-width: 220px;

    border: 2px solid #C8C8C8;
    border-radius: 15px;

    text-indent: .75em;

    &:focus,
    &:active{
      border-color: $color-primary;
    }
  }
}

.form-checkbox{
  display: grid;
  grid-template-columns: 16px 1fr;

  &__description{
    margin-top: 0.25em;
  }
}