.img{
  margin: 0;
  padding: 0;

  position: relative;

  &--centered{
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

@for $i from 1 through 100 {
  .img--shrink-#{$i} {
    img{
      width: percentage($i / 100);
    }
  }
}

.img-brand-banner{
  width: auto; 
  height: 30px;
}